#player {
background: #cccfef;
}

.header {
  box-sizing: border-box;
  padding: 15px 25px;
  border-bottom: 1px solid #ccc;
  background: #fff;
 
  
}
 .logo {
  vertical-align: middle;
 }
.caption {
  
  font-size: 20px;
  text-indent: 20px;
  display: inline-block;
}
.process {
 
  width: 100%;
  height: 3px;
  background: #ddd;
  cursor: pointer;
}
.process-bar {
  width: 0;
  height: 3px;
  background-color: #2f9842;
}

.player-page{
	width: 700px;
	margin: auto;
  margin-top: 100px;
  overflow: hidden;
}
	.desc {
		font-size: 16px;
		color: rgb(47, 152, 66);
	}

	.cover {
		width: 180px;
		height: 180px;
    margin-left: 20px;
   
  }
  .song-cover {
			width: 180px;
			height: 180px;
			border-radius: 50%;
		}
	
	.volume-container {
		position: relative;
		left: 20px;
    top: -3px;
    display: flex;
    align-items:center;
	}
	.volume-container .volume-wrapper {
		opacity: 0;
		-webkit-transition: opacity .5s linear;
		transition: opacity .5s linear;
	}
	.volume-container:hover .volume-wrapper {
		opacity: 1;
	}
	.music-title {
	    font-size: 25px;
	    font-weight: 400;
	    color: rgb(3, 3, 3);
	    height: 36px;
	    line-height: 36px;
	}
	.music-artist {
		font-size: 15px;
	    font-weight: 400;
	    color: rgb(74, 74, 74);
    }
    .left-time {
    	font-size: 14px;
    	color: #999;
    	font-weight: 400;
    	width: 40px;
    }
    .icon {
    	cursor: pointer;
    }
    .ml20 {
    	margin-left: 20px;
    }
    .mt35 {
    	margin-top: 35px;
    }
    .volume-wrapper {
    	width: 60px;
    	display: inline-block;
    }
    .icon-bofang {
      margin-top: 5px;
      font-size: 25px;
    }
    .icon-zanting, .icon-bofang {
      font-size: 20px;
    }

    .player-content {
      margin-top: 20px;
    }
    /* .song-pic {
      margin-left: 100px;
      
    } */
    .mt20{
      display: flex;
    }
    .icon-laba {
      font-size: 26px;
      
    }
    .iconfont {
      color: #999999;
    }
    .controll-wrapper {
      width: 600px;
    }
    .volume-wrapper {
      vertical-align: middle;
    }
